<template>
    <el-container style="height: 500px; border: 1px solid #eee">
        <el-header class="el-icon-s-home" style=" font-size: 40px; background-color:#409EFF; color:azure">
            教考系统</el-header>
        <el-footer>
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                <el-menu-item index="1"><router-link to="/student" tag="span">账号信息</router-link></el-menu-item>
                <el-menu-item index="2"><router-link to="/course" tag="span">课程表</router-link></el-menu-item>
                <el-menu-item index="3"><router-link to="/studentExam" tag="span">参加考试</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/score" tag="span">考试成绩查询</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/courseSelection" tag="span">选课</router-link></el-menu-item>

            </el-menu>
        </el-footer>


        <el-main>
            <div>
                        <div style="margin-bottom: 20px;">
                            <el-card v-for="detail in data.answerSheetDetails" :key="detail.detailId">
                                <div slot="header" class="clearfix">
                                    <span>题目 {{ detail.moduleExercise.exerciseInModuleNum }}</span>
                                </div>
                                <p><strong>题目描述:</strong> {{ detail.moduleExercise.exercise?.exerciseInfo }}</p>
                                <p><strong>题目选项:</strong> {{ detail.moduleExercise.exercise?.exerciseProblem }}</p>
                                <p><strong>学生答案:</strong> {{ detail.answer }}</p>
                                <p><strong>正确答案:</strong> {{ detail.moduleExercise.exercise?.exerciseAnswer }}</p>
                                <p><strong>评分:</strong> {{ detail.score }}
                                </p>
                            </el-card>
                        </div>
            </div>

            <!-- <div>
                <el-card v-for="(module, index) in examPaper.modules" :key="index" class="module-card">
                    <h4>模块 {{ module.moduleNum }} - 题型: {{ getExerciseTypeName(module.exerciseType) }}</h4>
                    <el-table :data="module.moduleExercises" style="width: 100%">
                        <el-table-column prop="exerciseInModuleNum" label="题号" width="80"></el-table-column>
                        <el-table-column prop="exercise.exerciseInfo" label="题目描述"></el-table-column>
                        <el-table-column prop="exercise.exerciseProblem" label="题目内容"></el-table-column>
                        <el-table-column prop="exercise.exerciseAnswer" label="正确答案"></el-table-column>
                    </el-table>
                </el-card>
            </div> -->
        </el-main>
    </el-container>
</template>

<script>
import axios from 'axios';

export default {
    props: {
        id: Number
    },
    data() {

        return {
            activeSheet: '1',
            // 测试数据：模拟的题目列表
            data: null, // 初始值为 null
            //学号
            studentID: '',

        }
    },
    mounted() {
        const storedID = sessionStorage.getItem('studentID');
        this.studentID = storedID;
        this.getAll();



    },
    methods: {
        //根据学号和考试编号查询答题卷详情表
        getAll() {
            axios.get(`/api/AnswerSheet/examSchemeNum/${this.id}/studentId/${this.studentID}`).then((result) => {
                if (result.data.code == 1 && result.data.data != null) {
                    this.data = result.data.data;
                }
            });
        },
    },
}
</script>

<style scoped>
.el-container {
    min-height: 100vh;
    /* 至少占据整个视口高度 */
    max-height: 1200px;
    /* 最大高度限制 */
    overflow-y: auto;
    /* 如果内容超出高度，显示滚动条 */
    display: flex;
    flex-direction: column;
}

.el-header,
.el-footer {
    flex: 0 0 auto;
    /* 固定高度 */
    background-color: #409EFF;
    color: azure;
    padding: 10px;
}

.el-main {
    flex: 1 1 auto;
    /* 自动占据剩余空间 */
    overflow-y: auto;
    /* 如果内容超出高度，显示滚动条 */
    padding: 20px;
    box-sizing: border-box;
}
</style>